	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Fly Into Kerry","date":"16 Feb 2004","slug":"/2004-02-16-fly-into-kerry/","tags":"Y2004"},"html":"<p><img alt=\"wonkette.png\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/02/wonkette.png\" width=\"143\" height=\"130\" border=\"0\" align=\"right\" /> Google ad seen at <a href=\"http://www.wonkette.com/\">Wonkette</a>, where you can get the latest scoop on Kerry's intern. One scoop that's not there... according to <a title=\"US-Wahlkampf: Deutsche Spur - Politik - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/politik/ausland/0,1518,286669,00.html\">Spiegel Online</a> she was in Nairobi to interview with the German news agency <a href=\"http://www.dpa.de/\">dpa</a>.</p>"},"next":{"fields":{"title":"A Manual for Germany","date":"16 Feb 2004","slug":"/2004-02-16-a-manual-for-germany/"}},"previous":{"fields":{"title":"Tobias Schwarz, A Fistful of Euros: Reforming Germany is not just hard. It is harder","date":"16 Feb 2004","slug":"/2004-02-16-tobias-schwarz-a-fistful-of-euros-reforming-germany-is-not-just-hard-it-is-harder/"}}},{"node":{"fields":{"title":"A Manual for Germany","date":"16 Feb 2004","slug":"/2004-02-16-a-manual-for-germany/","tags":"Y2004"},"html":"<p><a title=\"Der Schockwellenreiter\" href=\"http://schockwellenreiter.server-wg.de/blog/1781\">Via SWR</a>: <a title=\" We hope the word foreigner will become a foreign word for you as soon as possible...\" href=\"http://www.handbuch-deutschland.de/index_en.html\">A Manual for Germany</a></p>"},"next":{"fields":{"title":"Square","date":"15 Feb 2004","slug":"/2004-02-15-square/"}},"previous":{"fields":{"title":"Fly Into Kerry","date":"16 Feb 2004","slug":"/2004-02-16-fly-into-kerry/"}}},{"node":{"fields":{"title":"Square","date":"15 Feb 2004","slug":"/2004-02-15-square/","tags":"Y2004"},"html":"<p>My home town in Minnesota is exactly 1 mile square, so it almost completely fits on this 1500x1500m picture from <a title=\"ACME Mapper\" href=\"http://mapper.acme.com/\">ACME Mapper</a> (as seen at <a title=\"Don Park's Daily Habit - Home from Space\" href=\"http://www.docuverse.com/blog/donpark/EntryViewPage.aspx?guid=685c89e8-7c87-4b1f-96bb-2321305349f3\">Don Park</a>). Outside the town are just prairie fields. I spent pretty much my entire high school years within this square.  (Well, not entirely... otherwise I never would have met this German girl I later married....)</p>\n<p><img alt=\"luftbild.jpeg\" src=\"http://www.papascott.de/fotos/luftbild.jpeg\" width=\"405\" height=\"400\" border=\"0\" /></p>"},"next":{"fields":{"title":"Fear and change","date":"15 Feb 2004","slug":"/2004-02-15-fear-and-change/"}},"previous":{"fields":{"title":"A Manual for Germany","date":"16 Feb 2004","slug":"/2004-02-16-a-manual-for-germany/"}}}],"pathPrefix":"","first":false,"last":false,"index":528,"pageCount":1101}}

